.LoginPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 30%;
    margin: 30px auto;
}

.github-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    background: #424242;
    color: white;
    cursor: pointer;
    transition: 0.3s;
    font-size: 1.3em;
}

.github-btn:hover {
    background: #212121;
}

.skip-btn {
    margin-top: 20px;
}
