.DashBoardTable {
  margin-top: 50px;
  width: 99%;
  -webkit-box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.75);
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.75);
}

td {
  width: 10%;
  padding: 10px;
}

tr.border_bottom td {
  border-bottom: 1px solid black;
}

.complete-state {
  background: #4caf50;
}

.to-do-state {
  background: #bdbdbd;
}

.in-progress-state {
  background: #ffff00;
}

.checking-state {
  background: #e57373;
}

.checked-state {
  background: #d50000;
}

a {
    outline: none;
    text-decoration: none;
    color: black;
}
