body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.LoginPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 30%;
    margin: 30px auto;
}

.github-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    background: #424242;
    color: white;
    cursor: pointer;
    transition: 0.3s;
    font-size: 1.3em;
}

.github-btn:hover {
    background: #212121;
}

.skip-btn {
    margin-top: 20px;
}

.DashBoardTable {
  margin-top: 50px;
  width: 99%;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.75);
}

td {
  width: 10%;
  padding: 10px;
}

tr.border_bottom td {
  border-bottom: 1px solid black;
}

.complete-state {
  background: #4caf50;
}

.to-do-state {
  background: #bdbdbd;
}

.in-progress-state {
  background: #ffff00;
}

.checking-state {
  background: #e57373;
}

.checked-state {
  background: #d50000;
}

a {
    outline: none;
    text-decoration: none;
    color: black;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Select {
  width: 500px;
}

.score-board {
  margin-top: 50px;
  width: 800px;
  height: 300px;
  overflow-y: hidden;
}

.number {
  color: red;
}

